<template>
  <div>
    <v-btn 
    v-for="(link, key) in validLinks"
    :key="key"
    :color="color"
    plain 
    fab
    small 
    :href="link"
    target="_blank"
    >
      <v-icon>{{ `mdi-${key}` }}</v-icon>
    </v-btn>
  </div>
</template>

<script>
export default {
  props: {
    links: {
      type: Object,
      default: () => ({})
    },
    color: {
      type: String,
      default: "red"
    }
  },
  methods: {
    isValid(url) {
      return (url && url.trim() != '') 
    }
  },
  computed: {
    validLinks() {
      return Object.keys(this.links)
        .filter(key => this.isValid(this.links[key]))
        .reduce((result, key) => (result[key] = this.links[key], result), {});
    }
  }
}
</script>